.shopping-list-header {
  color: #111;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
}

body.dark-theme .shopping-list-header {
  color: #fff;
}

.empty-shopping-list-div {
  padding: 80px 55px;
}

.empty-shopping-list-msg {
  text-align: center;
  color: rgba(0, 0, 0, .6);
  margin-bottom: 14px;
}

.empty-shopping-list-img {
  width: 265px;
  height: 198px;
}

.title-color {
  color: #4f2ee8;
}

.support-ukraine {
  display: none;
}

@media screen and (min-width: 768px) {
  .shopping-list-header {
    font-size: 48px;
  }

  .shopping-list.section {
    padding-top: 0;
  }

  .empty-shopping-list-div {
    width: 437px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 100px;
  }

  .empty-shopping-list-msg {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .empty-shopping-list-img {
    width: 322px;
    height: 241px;
  }

  @media screen and (min-width: 1440px) {
    .shopping-list-div {
      width: 996px;
    }

    .container-shop {
      width: 1440px;
      justify-content: left;
      align-items: start;
      margin: 0 auto;
      padding: 0 24px;
      display: flex;
    }

    .shop-list-support-ukraine > section.support-ukraine {
      margin: 0;
      display: block;
    }

    div.shopping-list-div {
      padding-left: 40px;
    }

    .book-card-description {
      width: 808px;
    }

    .shopping-list-header {
      margin-top: 0;
    }
  }
}

